 .backdrop {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      //background-color: $background-color-bachdrop;
      z-index: 1;
      display: flex;
      transition: visibility 3000ms linear, opacity 3000ms linear;
    }
    .is-hidden {
      pointer-events: none;
      visibility: hidden;
      opacity: 0;
    
      & .modal-follow {
        transform: scale(0);
      }
    }
    .modal-follow {
      position: absolute;
      width: 272px;
      height: 380px;
      margin: auto;
      background-color: $color-light;
      text-align: center;
      z-index: 2;
    
      transition: transform 3000ms linear;
      transform: scale(1);
      &__btn--close {
        width: 18px;
        height: 18px;
        border: none;
        background-color: transparent;
        margin-top: 14px;
        margin-left: 240px;
        display: flex;
        position: absolute;
        padding: 0;
      }
      &__heading {
        font-family: $main-font;
        font-style: normal;
        font-weight: 600;
        font-size: 19px;
        line-height: 1.211;
        color: $color-dark;
        margin: 18px 16px 14px 16px;
      }
      &__paragraph {
        font-family: $main-font;
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 1.3;
        color: $modal-text;
        margin: 0 18px 18px 18px;
      }
      &__label {
        position: relative;
        display: flex;
        flex-direction: column-reverse;
        font-family: $main-font;
        font-weight: 500;
        font-size: 10px;
        line-height: 1.2;
        color: $modal-text;
        align-items: start;
        margin: 0 18px 18px 18px;
        gap: 8px;
      }
      &__icon--correct {
        position: absolute;
        right: 16px;
        bottom: 7px;
        fill: $correct;
        display: none;
      }
      &__icon--error {
        position: absolute;
        right: 16px;
        bottom: 7px;
        fill: $invalid;
        display: none;
      }
      &__input {
        // border: 1px solid $input-border-normal;
        width: 224px;
        border-radius: 4px;
        padding: 10px 0 10px 12px;
        font-size: 12px;
        line-height: 1.25;
        &:hover {
          // border-color: $input-border-hover;
        }
        &:focus-visible {
          outline: none;
        }
        &:not(:placeholder-shown):required:valid {
          border-color: $correct;
        }
        &:not(:placeholder-shown):required:valid + .modal-follow__icon--correct {
          display: flex;
        }
        &:not(:placeholder-shown):required:valid
          + .modal-follow__icon--correct
          + .modal-follow__icon--error
          + .modal-follow__text--correct {
          color: $correct;
        }
        &:not(:placeholder-shown):required:invalid {
          border-color: $invalid;
        }
        &:not(:placeholder-shown):required:invalid
          + .modal-follow__icon--correct
          + .modal-follow__icon--error {
          display: flex;
        }
        &:not(:placeholder-shown):required:invalid
          + .modal-follow__icon--correct
          + .modal-follow__icon--error
          + .modal-follow__text--error {
          color: $invalid;
        }
      }
      &__button {
        font-family: $main-font;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 1.214;
        border: none;
        background-color: $color-btn;
        border-radius: 8px;
        padding: 10px 82px;
        align-self: center;
        color: $color-light;
        &:hover {
          // background-color: $button-hover;
        }
        &:focus {
          // background-color: $button-focus;
        }
      }
    }
    @media (min-width: 768px) {
      .modal-follow {
        width: 588px;
        height: 644px;
      }
    }
    @media (min-width: 1280px) {
      .modal-follow {
        width: 860px;
        height: 528px;
        display: flex;
        flex-wrap: wrap;
      }
    }
    @media (min-width: 768px) {
      .modal-follow__btn--close {
        width: 24px;
        height: 24px;
        margin-top: 24px;
        margin-left: 540px;
      }
    }
    @media (min-width: 1280px) {
      .modal-follow__btn--close {
        margin-left: 812px;
      }
    }
    @media (min-width: 768px) {
      .modal-follow__heading {
        font-size: 28px;
        line-height: 1.214;
        margin: 28px 118px 24px 118px;
      }
    }
    @media (min-width: 1280px) {
      .modal-follow__heading {
        margin: 111px 40px 24px 40px;
      }
    }
    @media (min-width: 768px) {
      .modal-follow__paragraph {
        font-size: 16px;
        margin: 0px 126px 32px 126px;
      }
    }
    @media (min-width: 1280px) {
      .modal-follow__paragraph {
        margin: 0px 48px 32px 48px;
      }
    }
    @media (min-width: 768px) {
      .modal-follow__label {
        font-size: 14px;
        line-height: 1.214;
        margin: 0px 126px 28px 126px;
      }
    }
    @media (min-width: 1280px) {
      .modal-follow__label {
        margin: 0px 48px 28px 48px;
      }
    }
    @media (min-width: 768px) {
      .modal-follow__input {
        width: 336px;
        font-size: 14px;
        line-height: 1.214;
        padding: 16px 0 16px 12px;
      }
    }
    @media (min-width: 768px) {
      .modal-follow__button {
        font-size: 18px;
        line-height: 1.222;
        padding: 14px 122px;
      }
    }
    @media (min-width: 1280px) {
      .modal-follow__dt {
        width: 432px;
      }
    }
    @media (min-width: 768px) {
      .modal-follow__icon--correct {
        bottom: 14px;
      }
    }
    @media (min-width: 768px) {
      .modal-follow__icon--error {
        bottom: 14px;
      }
    }